.container {
  flex-basis: 100%;
  display: flex;
  flex-direction: column;
  background: hsl(0, 0%, 100%);
  display: flex;
  margin: 25vh auto;
  border-radius: 20px;
  padding: 1rem;
}

@media (min-width: 660px) {
  .container {
    flex-basis: 75%;
    flex-direction: row;
  }
}
